.button
	position: relative
	display: inline-block
	white-space: nowrap
	cursor: pointer
	box-shadow: none
	border-width: 2px
	border-style: solid
	padding: 10px 32px
	border-radius: 96px
	color: var(--color-dark-1)
	+trans2
	transition-property: color, background-color, border-color
	outline: none
	&:focus
		outline: none
	&:hover
		.button__label-normal
			.button__title
				transform: translate(0%, -100%)
				opacity: 0
			.button__icon
				opacity: 0
				transform: translate(100%, 0%)
		.button__label-hover
			.button__title
				transform: translate(0%, 0%)
				opacity: 1
			.button__icon
				opacity: 1
				transform: translate(0%, 0%)
	*
		pointer-events: none

.button_icon
	display: inline-flex
	border: none
	padding: 10px 24px
	@media screen and (max-width: $md)
		padding: 10px 20px

	.button__label
		padding: 0
.button__label-hover
	position: absolute
	top: 50%
	left: 50%
	transform: translate(-50%, -50%)
	.button__title
		transform: translate(0%, 100%)
		opacity: 0
	.button__icon
		transform: translate(-100%, 0%)
		opacity: 0

.button__label-normal
	.button__title
		transform: translate(0%, 0%)
		opacity: 1

.button__title
	display: inline-block
	transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out
	letter-spacing: -0.5px
.button__label
	display: inline-block
	+fluid('font-size', 15, 20)
	font-weight: 600

.button__icon
	display: inline-flex
	align-items: center
	justify-content: center
	transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out
	vertical-align: middle
	margin-bottom: 0.1em
	+fluid('font-size', 26, 38)
	line-height: 1

.button__icon_before
	margin-right: 0.1em
.button__icon_after
	margin-left: 0.25em
	margin-right: -0.5em

.button_fullwidth
	width: 100%
	padding: 6px 0px
.button-fullwidth
	.button
		width: 100%
	.button__label
		+fluid('font-size', 14, 17)
		font-weight: 700

.padding_big
	padding: 12px 40px

.fs-big
	.button__label
		+fluid('font-size', 17, 22)

.button-big
	padding: 22px 50px
	border-radius: 8px
	width: 100%
	display: flex
	justify-content: center
	@media screen and (max-width: $sm)
		padding: 18px 30px

	.button__label
		display: flex
		+fluid('font-size', 20, 26)
		line-height: 1.15
	.button__icon_before
		margin-right: 6px
	.button__icon
		+fluid('font-size', 44, 54)
